import React from 'react'
import {Grid,Paper,Avatar,TextField, Typography} from '@material-ui/core'
import LockOutlinedIcon from '@material-ui/icons/Lock';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Button from '@material-ui/core/Button';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';





function Login() {


    const paperstyle={padding:20,height:'70vh',width:280,margin:'20px auto'}
    const avatarStyle={backgroundColor:'#4387ff'}
    const btnStyle={margin:'8px 0'}
  return (
    <div>
        <Grid>
        <Paper style={paperstyle} elevation={10} >
            <grid align='center' >
                <Avatar style={avatarStyle}><LockOutlinedIcon/></Avatar>
                <h2>Sign In</h2>
            </grid>

            <TextField label='UserName' placeholder='Enter User Nmae' fullWidth required/>
            <TextField label='Password' placeholder='Enter Password' type='password' fullWidth required/>
    <FormControlLabel
        control={
          <Checkbox
            
            name="checkedB"
            color="primary"
          />

        }
        label="Remember me"
      />
      <Button type='submit' fullWidth Button variant="contained" color="primary" style={btnStyle} disableElevation>Submit</Button>
      <Typography>
        <Link href="#" > Forgot Password </Link>
        </Typography>
        <Typography>Do you Have An Account
        <Link href="#" > Sign Up! </Link>
        </Typography>
        </Paper>

        </Grid>
    </div>
  )
}

export default Login